	module.exports = {"data":{"site":{"siteMetadata":{"title":"5π - fish's blog","author":"Johannes 'fish' Ziemke"}},"markdownRemark":{"id":"/usr/src/src/pages/2015/09/02/uploaded-thousands-of-photos-to-google-photos-here-are-the-best-classification-mistakes/index.md absPath of file >>> MarkdownRemark","html":"<p>I’m a lazy person who never took time to organize their photos so I got really excited about Google Photos and just finished uploading all my Photos to it. I used flickr in the past but just dumping photos there, even if I can search for EXIF tag data doesn’t make much difference to storing them on a hard disk in a drawer.</p>\n<p>Google Photo on the other hand <em>understands</em> what’s going on in a picture.\nGoogle uses a <a href=\"https://medium.com/backchannel/how-google-s-new-photos-app-can-tell-cats-from-dogs-ffd651dfcd80\">neural network</a> which is trained by millions of photos on the web to identify objects in pictures.</p>\n<p>This works very well and when thinking about it’s mistakes, the uncanny feeling just amplifies: They are mistakes, they aren’t bugs. It’s not faulty, it’s just a little unexperienced - almost like a child.\nIn fact, I would be very interested in a catogorization standoff between a child and Google.</p>\n<h1>Concerts</h1>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/Screen-Shot-2015-09-02-at-14-42-18-36bddfe74c91b79319b26a9fd726c4a5-f69b5.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 92.17506631299734%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Screen Shot 2015 09 02 at 14 42 18\"\n        title=\"\"\n        src=\"/static/Screen-Shot-2015-09-02-at-14-42-18-36bddfe74c91b79319b26a9fd726c4a5-fb8a0.png\"\n        srcset=\"/static/Screen-Shot-2015-09-02-at-14-42-18-36bddfe74c91b79319b26a9fd726c4a5-1a291.png 148w,\n/static/Screen-Shot-2015-09-02-at-14-42-18-36bddfe74c91b79319b26a9fd726c4a5-2bc4a.png 295w,\n/static/Screen-Shot-2015-09-02-at-14-42-18-36bddfe74c91b79319b26a9fd726c4a5-fb8a0.png 590w,\n/static/Screen-Shot-2015-09-02-at-14-42-18-36bddfe74c91b79319b26a9fd726c4a5-526de.png 885w,\n/static/Screen-Shot-2015-09-02-at-14-42-18-36bddfe74c91b79319b26a9fd726c4a5-fa2eb.png 1180w,\n/static/Screen-Shot-2015-09-02-at-14-42-18-36bddfe74c91b79319b26a9fd726c4a5-f69b5.png 1508w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \nI’m really not sure about this one.</p>\n<h1>Dessert</h1>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/Screen-Shot-2015-09-02-at-14-44-38-305de6716396787bfbf295278dda2def-474b7.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 81.25819134993446%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Screen Shot 2015 09 02 at 14 44 38\"\n        title=\"\"\n        src=\"/static/Screen-Shot-2015-09-02-at-14-44-38-305de6716396787bfbf295278dda2def-fb8a0.png\"\n        srcset=\"/static/Screen-Shot-2015-09-02-at-14-44-38-305de6716396787bfbf295278dda2def-1a291.png 148w,\n/static/Screen-Shot-2015-09-02-at-14-44-38-305de6716396787bfbf295278dda2def-2bc4a.png 295w,\n/static/Screen-Shot-2015-09-02-at-14-44-38-305de6716396787bfbf295278dda2def-fb8a0.png 590w,\n/static/Screen-Shot-2015-09-02-at-14-44-38-305de6716396787bfbf295278dda2def-526de.png 885w,\n/static/Screen-Shot-2015-09-02-at-14-44-38-305de6716396787bfbf295278dda2def-fa2eb.png 1180w,\n/static/Screen-Shot-2015-09-02-at-14-44-38-305de6716396787bfbf295278dda2def-474b7.png 1526w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \nLet’s finish this course with a nice bowl of raw chicken.</p>\n<h1>Dogs</h1>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/Screen-Shot-2015-09-02-at-14-40-14-b6300e467befbb18c0a227ecbbd91c04-5daa8.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 83.35500650195058%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Screen Shot 2015 09 02 at 14 40 14\"\n        title=\"\"\n        src=\"/static/Screen-Shot-2015-09-02-at-14-40-14-b6300e467befbb18c0a227ecbbd91c04-fb8a0.png\"\n        srcset=\"/static/Screen-Shot-2015-09-02-at-14-40-14-b6300e467befbb18c0a227ecbbd91c04-1a291.png 148w,\n/static/Screen-Shot-2015-09-02-at-14-40-14-b6300e467befbb18c0a227ecbbd91c04-2bc4a.png 295w,\n/static/Screen-Shot-2015-09-02-at-14-40-14-b6300e467befbb18c0a227ecbbd91c04-fb8a0.png 590w,\n/static/Screen-Shot-2015-09-02-at-14-40-14-b6300e467befbb18c0a227ecbbd91c04-526de.png 885w,\n/static/Screen-Shot-2015-09-02-at-14-40-14-b6300e467befbb18c0a227ecbbd91c04-fa2eb.png 1180w,\n/static/Screen-Shot-2015-09-02-at-14-40-14-b6300e467befbb18c0a227ecbbd91c04-5daa8.png 1538w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \nTo be fair, you could consider <a href=\"https://en.wikipedia.org/wiki/British_Shorthair\">british shorthair</a> the dogs of cats.</p>\n<h1>Birds</h1>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/Screen-Shot-2015-09-02-at-14-39-47-76ffd3846a9708abec735e4b77acd861-585a2.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 81.14209827357239%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Screen Shot 2015 09 02 at 14 39 47\"\n        title=\"\"\n        src=\"/static/Screen-Shot-2015-09-02-at-14-39-47-76ffd3846a9708abec735e4b77acd861-fb8a0.png\"\n        srcset=\"/static/Screen-Shot-2015-09-02-at-14-39-47-76ffd3846a9708abec735e4b77acd861-1a291.png 148w,\n/static/Screen-Shot-2015-09-02-at-14-39-47-76ffd3846a9708abec735e4b77acd861-2bc4a.png 295w,\n/static/Screen-Shot-2015-09-02-at-14-39-47-76ffd3846a9708abec735e4b77acd861-fb8a0.png 590w,\n/static/Screen-Shot-2015-09-02-at-14-39-47-76ffd3846a9708abec735e4b77acd861-526de.png 885w,\n/static/Screen-Shot-2015-09-02-at-14-39-47-76ffd3846a9708abec735e4b77acd861-fa2eb.png 1180w,\n/static/Screen-Shot-2015-09-02-at-14-39-47-76ffd3846a9708abec735e4b77acd861-585a2.png 1506w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \nBirds. That’s definitely correct.</p>\n<h1>Skylines</h1>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/Screen-Shot-2015-09-02-at-14-49-11-f9f2873c3d2aea66273e2893b6cf2f89-5daa8.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 92.71781534460338%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Screen Shot 2015 09 02 at 14 49 11\"\n        title=\"\"\n        src=\"/static/Screen-Shot-2015-09-02-at-14-49-11-f9f2873c3d2aea66273e2893b6cf2f89-fb8a0.png\"\n        srcset=\"/static/Screen-Shot-2015-09-02-at-14-49-11-f9f2873c3d2aea66273e2893b6cf2f89-1a291.png 148w,\n/static/Screen-Shot-2015-09-02-at-14-49-11-f9f2873c3d2aea66273e2893b6cf2f89-2bc4a.png 295w,\n/static/Screen-Shot-2015-09-02-at-14-49-11-f9f2873c3d2aea66273e2893b6cf2f89-fb8a0.png 590w,\n/static/Screen-Shot-2015-09-02-at-14-49-11-f9f2873c3d2aea66273e2893b6cf2f89-526de.png 885w,\n/static/Screen-Shot-2015-09-02-at-14-49-11-f9f2873c3d2aea66273e2893b6cf2f89-fa2eb.png 1180w,\n/static/Screen-Shot-2015-09-02-at-14-49-11-f9f2873c3d2aea66273e2893b6cf2f89-5daa8.png 1538w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \nBar graph or 8-bit skylines?</p>","frontmatter":{"title":"Uploaded thousands of Photos to Google Photos, here are the best classification mistakes","date":"2015-09-02 13:38:13 +0000"}}},"pathContext":{"slug":"/2015/09/02/uploaded-thousands-of-photos-to-google-photos-here-are-the-best-classification-mistakes/","previous":{"fields":{"slug":"/2015/08/31/dont-manage-config-unless-you-have-to/"},"frontmatter":{"title":"Don't manage configuration unless you have to."}},"next":{"fields":{"slug":"/2015/10/02/the-future/"},"frontmatter":{"title":"The Future"}}}}

